'use strict';

import axe from "axe-core";

axe
    .run(
        {
            rules: {
                'color-contrast': {enabled: true},
                'valid-lang': {enabled: true},
                'valid-lang-value': {enabled: true},
            },
            runOnly: {
                type: 'tag',
                values: ['wcag2a', 'wcag2aa', 'wcag2aaa', 'section508', 'best-practice', 'section508-a', 'section508-aaa',],
            },
            exclude: [
                '#wcag2a-errors',
                '#wcag2aa-errors',
                '#wcag2aaa-errors',
                '#wcag2a-warnings',
                '#wcag2aa-warnings',
                '#wcag2aaa-warnings',
                '#wcag2a-incomplete'
            ],

        },
    )
    .then(results => {
        const resultsContainer = document.getElementById('results');
        resultsContainer.innerHTML = 'p' + JSON.stringify(results.violations);
        results.violations.forEach(violation => {
            console.table(violation, ['id', 'impact', 'description', 'help', 'helpUrl', 'tags', 'nodes', 'data']);
        });
        results.incomplete.forEach(test => {
            console.table(test);
        });

    });




